.mt-userbar{position:fixed;transform:translate3d(0, 0, 0);right:.5em;top:50%;margin-top:-7em;z-index:98;width:3em;border-radius:1.5em;padding:1em .5em;background-color:rgba(0,0,0,.85);display:flex;align-items:stretch}@media screen and (max-width: 767px){.mt-userbar{display:none}}.mt-userbar ul{display:flex;flex-flow:row wrap;align-items:center;justify-content:center;width:100%}.mt-userbar ul li{width:1.5em;margin:.5em auto;flex-grow:1;flex-shrink:1;flex-basis:1.5em;min-height:2.1em;display:flex;justify-content:center;align-items:center}.mt-userbar ul li a{display:block;color:#ddd;position:relative;text-align:center;transition:.3s ease-out}.mt-userbar ul li a:hover{color:#e352b2;text-shadow:0px 0px 5px rgba(0,0,0,.75)}.mt-userbar ul li a:hover span{opacity:1;pointer-events:normal;left:-9em}.mt-userbar ul li a i{font-size:1.25em}.mt-userbar ul li a span{position:absolute;opacity:0;pointer-events:none;background:#e352b2;padding:.25em;color:#fff;left:-12em;width:8em;top:-0.25em;font-size:.813em;border-radius:.25rem;transition:.3s ease-out;color:#fff}.mt-userbar ul li a.hasItem{color:#e352b2;position:relative}.mt-userbar ul li a.hasItem::after{content:"";position:absolute;width:10px;height:10px;right:-3px;top:-4px;border-radius:50%;background-color:#ffcc31}

